/**
 * 获取所有item的translateY的值
 * @param {number} translateY  当前的偏移值
 * @param el
 * @param viewEl
 * @returns
 */
export var getItemsTranslateY = function (translateY, optionsId, el, viewEl) {
    if (!el || !viewEl) {
        return;
    }
    /**
     * 视口的 rect;
     */
    var viewRect = viewEl.getBoundingClientRect();
    /**
     * 所有的Item
     */
    var items = el.getElementsByClassName("picker_item");
    /**
     *
     */
    var arr = [];
    for (var i = 0; i < items.length; i++) {
        var item = items[i];
        var itemRect = item.getBoundingClientRect();
        var y = viewRect.top - itemRect.top + translateY;
        arr.push({
            translateY: y,
            height: item.offsetHeight,
            id: optionsId[i],
        });
    }
    return arr;
};
