import React from "react";
/**
 * 获取年列表
 */
export var getYearList = function (end, start) {
    if (start === void 0) { start = 1970; }
    var nodes = [];
    for (var i = start; i <= end; i++) {
        nodes.push({
            id: i.toString(),
            content: React.createElement("span", { className: "calendarMobile_itemContent" },
                i,
                "\u5E74"),
        });
    }
    return nodes;
};
