import React from "react";
/**
 * 获取日列表
 */
export var getDays = function (year, month) {
    var nodes = [];
    var days = new Date(Number(year), Number(month), 0).getDate();
    // console.log("year", year, "month", Number(month) - 1, "days", days);
    for (var i = 1; i <= days; i++) {
        nodes.push({
            id: i.toString(),
            content: React.createElement("span", { className: "calendarMobile_itemContent" },
                i,
                "\u65E5"),
        });
    }
    return nodes;
};
