/**
 * @file 解决 IEEE 754导致的精度丢失的bug
 * @date 2023-01-16
 * @author xuejie.he
 * @lastModify xuejie.he 2023-01-16
 */
/**
 * 找出最小的number
 */
var findMin = function (arr) {
    var min = arr[0];
    for (var i = 1; i < arr.length; i++) {
        var val = arr[i];
        if (val < min) {
            min = val;
        }
    }
    return min;
};
/**
 * 获取小数位
 * @param args
 */
var fetchDecimal = function (num) {
    var numStr = num.toString();
    return numStr.includes(".") ? numStr.split(".")[1].length : 0;
};
/**
 * 将所有的数字扩大相应的倍数
 *
 * 产生新的数字
 */
var toBigForNumber = function (arr, type) {
    var minVal = findMin(arr);
    var length = fetchDecimal(minVal);
    var expansion = Math.pow(10, length);
    var total = arr[0] * expansion;
    for (var i = 1; i < arr.length; i++) {
        var value = arr[i] * expansion;
        if (type === "-") {
            total -= value;
        }
        else {
            total += value;
        }
    }
    return total / expansion;
};
/**
 * 相加
 */
export var sum = function () {
    var args = [];
    for (var _i = 0; _i < arguments.length; _i++) {
        args[_i] = arguments[_i];
    }
    return toBigForNumber(args, "+");
};
/**
 * 相减
 */
export var sub = function () {
    var args = [];
    for (var _i = 0; _i < arguments.length; _i++) {
        args[_i] = arguments[_i];
    }
    return toBigForNumber(args, "-");
};
/**
 * 相乘
 */
export var mul = function () {
    var args = [];
    for (var _i = 0; _i < arguments.length; _i++) {
        args[_i] = arguments[_i];
    }
    /**
     * 扩大多少
     */
    var expansion = 1;
    var value = args[0];
    var length = fetchDecimal(value);
    expansion = Math.pow(10, length);
    value *= expansion;
    for (var i = 1; i < args.length; i++) {
        var item = args[i];
        var itemLength = fetchDecimal(item);
        if (itemLength) {
            var itemExpansion = Math.pow(10, itemLength);
            item *= itemExpansion;
            expansion *= itemExpansion;
        }
        value *= item;
    }
    return value / expansion;
};
/**
 * 相除
 * @param numerator 分子
 * @param denominator 分母
 */
export var toDiv = function (numerator, denominator) {
    var minNum = findMin([numerator, denominator]);
    /**
     * 小数点后的位数
     */
    var length = fetchDecimal(minNum);
    /**
     * 扩大多少个10的阶乘
     */
    var lnVal = Math.pow(10, length);
    return (numerator * lnVal) / (denominator * lnVal);
};
